.page-header {
  margin-right: 120px;
  margin-left: 120px;
  margin-bottom: 80px;
  margin-top: 38px;
  display: flex;
  justify-content: space-between;
}

.page-header__logo {
  fill: #192252;
  margin-top: 8px;
}
.page-header__logo:hover {
  fill: #0e1734;
  border-bottom: 3px solid #192252;
}
.page-header__logo:active {
  opacity: 0.3;
}

.main-nav {
  align-self: center;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.main-nav__item:not(:last-child) {
  margin-right: 32px;
}

.main-nav__link {
  text-decoration: none;
  cursor: pointer;
  color: #192252;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}
.main-nav__link:hover {
  color: #0e1734;
  text-decoration: underline;
}
.main-nav__link:active {
  opacity: 0.3;
}

.promo {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.promo__title {
  margin: 0;
  padding: 0;
  margin-top: 135px;
  margin-bottom: 22px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 72px;
  letter-spacing: -0.01em;
  padding-right: 80px;
}

.promo__description {
  margin: 0;
  padding: 0;
  margin-bottom: 34px;
  padding-right: 120px;
}

.promo__image-wrapper {
  display: flex;
  justify-content: end;
  margin-bottom: 96px;
}

.promo__image {
  display: block;
}
.promo__image:not(:last-child) {
  margin-right: 24px;
}
.promo__image:nth-child(2) {
  padding-top: 80px;
}

.about-us {
  padding-top: 80px;
  padding-bottom: 80px;
  display: grid;
  grid-template-columns: 384px auto;
  gap: 120px;
}

.about-us__image {
  margin-right: 120px;
}

.about-us__title {
  margin: 0;
  padding: 0;
  margin-top: 105px;
  margin-bottom: 24px;
}

.about-us__description {
  margin: 0;
  padding: 0;
  padding-right: 100px;
  padding-bottom: 16px;
}

.about-us__button {
  margin-top: 16px;
}

.benefits {
  margin-top: 80px;
  margin-bottom: 80px;
}

.benefits__card {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}

.benefits__description {
  width: 580px;
}

.benefits__image--left {
  order: -1;
}

.reservation {
  margin-top: 80px;
  margin-bottom: 80px;
}

.reservation__wrapper {
  margin-left: 300px;
  margin-right: 300px;
}

.reservation__title {
  margin: 0 auto;
  margin-bottom: 24px;
  padding: 0;
  text-align: center;
}

.reservation__description {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 32px;
}

.reservation__button {
  margin: 0 auto;
  margin-bottom: 88px;
}

.reservation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.reservation__item {
  width: 384px;
}
.reservation__item:not(:last-child) {
  margin-right: 24px;
}

.reservation__item-title {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}

.reservation__item-description {
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
}

.reservation__item-link {
  color: #424f7b;
  text-decoration: none;
  cursor: pointer;
}
.reservation__item-link:hover {
  color: #0e1734;
  text-decoration: underline;
}
.reservation__item-link:active {
  opacity: 0.3;
}

.room {
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
  margin-bottom: 80px;
}

.room__wrapper {
  margin-top: 80px;
}

.room__subtitle {
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
}

.room__title {
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
}

.room__description {
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
  padding-right: 280px;
}

.room__links-wrapper {
  display: flex;
}

.room__button {
  margin-right: 24px;
}

.room__link {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #0e1734;
  text-decoration: none;
  display: block;
  align-self: center;
}

.room__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.room__item {
  padding: 32px 36px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.07);
  position: relative;
}

.room__item-info {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: rgba(255, 98, 10, 0.12);
  position: absolute;
  right: 36px;
  top: 30px;
  cursor: pointer;
}
.room__item-info:hover {
  background-color: #fab047;
}
.room__item-info:active {
  opacity: 0.6;
}
.room__item-info--active {
  background-color: #ff620a;
}

.room__item-info-arrow {
  fill: none;
  stroke: #ff620a;
  stroke-width: 3px;
}
.room__item-info-arrow--active {
  transform: rotate(-180deg);
  stroke: #ffffff;
}

.room_item-description {
  margin: 0;
  padding: 0;
  margin-top: 24px;
}

.feedbacks {
  padding-top: 80px;
  padding-bottom: 80px;
}

.feedbacks__wrapper {
  margin-right: 200px;
  margin-left: 200px;
}

.feedbacks__title {
  margin: 0;
  padding: 0;
  margin-bottom: 62px;
  text-align: center;
}

.feedbacks__avatar {
  margin: 0 auto;
  margin-bottom: 44px;
  display: block;
}

.feedbacks__text {
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  text-align: center;
}

.feedbacks__author {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #192252;
  margin-bottom: 24px;
}

.feedbacks__button-list {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.feedbacks__button-item {
  margin-right: 12px;
  align-self: center;
}
.feedbacks__button-item:last-child {
  margin-right: 0;
}

.feedbacks__button {
  border-radius: 50%;
  width: 12px;
  height: 12px;
  border: none;
  background-color: rgba(250, 176, 71, 0.35);
  cursor: pointer;
  display: block;
}
.feedbacks__button--active {
  background-color: #fab047;
}
.feedbacks__button:hover {
  background: #ff620a;
}
.feedbacks__button:active {
  opacity: 0.6;
}

.page-footer {
  background-color: #08265a;
  padding: 120px;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
}

.page-footer__logo {
  fill: #ffffff;
  margin-bottom: 12px;
}
.page-footer__logo:hover {
  fill: #424f7b;
}
.page-footer__logo:active {
  opacity: 0.3;
}

.page-footer__text {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 28px;
  color: #d9dbde;
  margin-bottom: 36px;
}

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.page-footer__social-item:not(:last-child) {
  margin-right: 32px;
}

.page-footer__social-logo {
  fill: #ffffff;
}
.page-footer__social-logo:hover {
  fill: #424f7b;
}
.page-footer__social-logo:active {
  opacity: 0.3;
}

.page-footer__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.page-footer__nav-item {
  margin-right: 94px;
}
.page-footer__nav-item:nth-child(2) {
  margin-right: 141px;
}
.page-footer__nav-item:last-child {
  margin-right: 0;
}

.page-footer__nav-item-link {
  margin-bottom: 24px;
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.01em;
  text-decoration: none;
  color: #ffffff;
}
.page-footer__nav-item-link:hover {
  color: #424f7b;
  text-decoration: underline;
  font-weight: 700;
}
.page-footer__nav-item-link:active {
  opacity: 0.3;
}

.page-footer__nav-sublist {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.page-footer__nav-subitem {
  margin-bottom: 24px;
}
.page-footer__nav-subitem:hover {
  color: #424f7b;
}
.page-footer__nav-subitem:active {
  opacity: 0.3;
}

.page-footer__nav-item-sublink {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.01em;
  text-decoration: none;
  color: #ffffff;
}
.page-footer__nav-item-sublink:hover {
  color: #424f7b;
  text-decoration: underline;
}
.page-footer__nav-item-sublink:active {
  opacity: 0.3;
}

