body {
  background: #ffffff;
  color: #192252;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 27px;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
}

.page-main {
  margin-left: 120px;
  margin-right: 120px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button--common {
  text-decoration: none;
  color: #ffffff;
  background: #0e1734;
  display: block;
  height: 48px;
  padding: 0;
  border-color: transparent;
  border-radius: 4px;
  cursor: pointer;
  width: 120px;
}
.button--common:hover {
  background: #08265a;
}
.button--common:active span {
  opacity: 0.3;
}

.button--common-text {
  display: block;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
}

.title--common {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 54px;
  letter-spacing: -0.01em;
}

.description--common {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  color: #848fac;
}